import React, { useState } from "react";
import { useFormik } from "formik";
import * as Yup from 'yup';
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Rings } from "react-loader-spinner";

function VerifyCode(){

    const [message, setMessage] = useState(null);
    const [errorMessage, setErrorMessage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    let navigate = useNavigate();

    let MyValSchema = Yup.object({
        resetCode: Yup.string().required("OTP is Required"),
    })

    const formik = useFormik({
        initialValues: {
            resetCode: "",
        }, validationSchema: MyValSchema,
        onSubmit: (values) => {
            formSendOTP(values)
        }
    })

    async function formSendOTP(values){
        setIsLoading(true)
        return await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode`, values).then((data) => {
            console.log(data);
            console.log(data.data.status);
        if(data.data.status === "success"){
            setMessage(data.data.status)
            navigate("/resetpassword")
            setIsLoading(false)
        }   
        }).catch((err) => {
            console.log(err);
            setErrorMessage(err.response.data.message)
            setIsLoading(false)
        })
        
    }

    return(
        <div style={{height: '40vh'}}>
            <div className="container  my-5 py-5">
                <div className="w-100 mx-auto">
                    {isLoading ? <div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                            </div>: ""}
                    <form onSubmit={formik.handleSubmit}>
                        <h2><strong>please enter your verification code</strong></h2>
                        {message ? <div className="alert alert-success">{message}</div> : ""}
                        {errorMessage ? <div className="alert alert-danger">{errorMessage}</div> : ""}

                        {formik.touched.resetCode && formik.errors.resetCode ? <div className=" alert alert-danger">{formik.errors.resetCode}</div> : ""}
                        <input type="tel" name="resetCode" id="resetCode" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.resetCode} placeholder="OTP"/>

                        {isLoading ? <button type="submit" className="btn bg-main text-white"><i className="fa fa-spin fa-spinner"></i></button> : <button type="submit" disabled={!(formik.isValid && formik.dirty)} className="btn btn-outline-success">Verify</button>}                        

                    </form>
                </div>
            </div>
        </div>
    );

}

export default VerifyCode;