import React, { useContext, useState } from "react";
import style from './Payment.module.css';
import { useFormik } from "formik";
import * as Yup from 'yup';
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
import { Zoom, toast } from "react-toastify";
import { TokenContext } from "../../../Context/TokenContext";

function Payment(){

    const [message, setMessage] = useState(null);
    const [errorMessage, setErrorMessage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);

    let {token} = useContext(TokenContext);

    let navigate = useNavigate();

    let {cartId} = useParams();

    let MyValSchema = Yup.object({
        details: Yup.string().required("Details is Required").min(5, "Min is 5 Chars").max(30, "Max is 30 Chars"),
        phone: Yup.string().required("Phone is Required").matches(/^01[0125][0-9]{8}$/, "Phone must be 11 Numbers"),
        city: Yup.string().required("City is Required").matches(/^[A-z][a-z]{3,}$/, "City must be more than 4 Chars"),
    })

    const formik = useFormik({
        initialValues: {
            details: "",
            phone: "",
            city: "",
        }, validationSchema: MyValSchema,
        onSubmit: (values) => {
            formPayment(values)
        }
    })

    async function formPayment(values){
        setIsLoading(true)
        const shippingAddresss = {
            shippingAddress: {
                details: values.details,
                phone: values.phone,
                city: values.city
            }
        };

        const url = `https://routeegy.github.io/Ecommerce`;
        return await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}?url=${url}`, shippingAddresss).then((data) => {
            console.log(data);
        if(data.data.message === "success"){
            setMessage(data.data.message)
            navigate("/login")
            setIsLoading(false)
        }   
        }).catch((err) => {
            setErrorMessage(err.response.data.message)
            toast.success(err.response.data.message, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });
            setIsLoading(false)
        })
    }

    async function formPayment2({id, shippingAddress}){
        setIsLoading(true)
        return await axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000`, {shippingAddress},{
            headers:{
                "Content-Type": "application/json",
                token
            }
        })
    }

    return(
        <div style={{height: '100px'}}>
            <div className="container  my-5 py-5">
                <div className="w-75 mx-auto">
                    <form onSubmit={formik.handleSubmit}>
                        {message ? <div className="alert alert-success">{message}</div> : ""}
                        {errorMessage ? <div className="alert alert-danger">{errorMessage}</div> : ""}
                        <label htmlFor="details">Details: </label>
                        {formik.touched.details && formik.errors.details ? <div className=" alert alert-danger">{formik.errors.details}</div> : ""}
                        <input type="text" name="details" id="details" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.details}/>

                        <label htmlFor="phone">Phone: </label>
                        {formik.touched.phone && formik.errors.phone ? <div className=" alert alert-danger">{formik.errors.phone}</div> : ""}
                        <input type="text" name="phone" id="phone" className="form-control mb-4" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.phone}/>
                        
                        <label htmlFor="city">City: </label>
                        {formik.touched.city && formik.errors.city ? <div className=" alert alert-danger">{formik.errors.city}</div> : ""}
                        <input type="text" name="city" id="city" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.city}/>
                        
                        {isLoading ? <button type="submit" className="btn bg-main text-white"><i className="fa fa-spin fa-spinner"></i></button> : <button disabled={!(formik.isValid && formik.dirty)} type="submit" className="btn btn-outline-primary">Pay now</button>}                        
                    </form>
                </div>
            </div>
        </div>
    );

}

export default Payment;