import React, { useEffect, useState } from "react";
import style from './Categories.module.css';
import axios from 'axios';
import { useQuery } from 'react-query';
import { Rings } from "react-loader-spinner";

function Categories(){

    const [catName, setCatName] = useState("");
    const [subCatNames, setSubCatNames] = useState([])
    const [subCatName, setSubCatName] = useState("")
    const [isSubCatLoading, setIsSubCatLoading] = useState(true)


    function getCategories(){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
    }

    let { data, isLoading } = useQuery("AllCategories", getCategories)

    function getCategoryName(categoryId, subCategoryName) {
        setCatName(categoryId);
        setSubCatName(subCategoryName);
    }

    async function getSubCategories(){
        let { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories/${catName}/subcategories`)
        console.log(data?.data);
        setSubCatNames(data?.data)
        setIsSubCatLoading(false);
    }

    useEffect(()=>{
        if(catName){
            getSubCategories();
        }
    },[catName])

    return(
        <div>
            <div className="container pt-5 d-flex align-items-center justify-content-center my-5 py-5">
                <div className="row g-4">
                    {isLoading ? (<div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                    </div>): ""}
                   {data?.data?.data?.map((prod) => (
                        <div key={prod._id} className="col-md-4">
                            <div className="product py-2 px-3" style={{transition: 'all 0.5s'}}>
                                <div onClick={() => getCategoryName(prod._id, prod.name, setSubCatName)}>
                                    <img src={prod.image} style={{height: '300px', objectFit: 'cover'}} className="w-100" alt=""/>
                                    <h2 className="text-main text-center" style={{backgroundColor: 'white', padding: '16px'}}><strong>{prod.name}</strong></h2>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
            {isSubCatLoading && catName ? (<div className="d-flex align-items-center justify-content-center">
                <Rings
                    visible={true}
                    height="480"
                    width="480"
                    color="#4fa94d"
                    ariaLabel="rings-loading"
                    wrapperStyle={{}}
                    wrapperClass=""
                />
                </div>): 
                ( 
                    <div className="container ">
                        {isSubCatLoading ? '' : <h2 className="text-center text-main "><strong>{subCatName} subcategories</strong></h2>}
                        <div className="row">
                            {subCatNames?.map((prod) => (
                                <div className="product col-md-4 py-2 px-3" style={{transition: 'all 0.5s'}} key={prod._id}>
                                <div>
                                    <h2 className="text-center" style={{backgroundColor: 'white', padding: '16px'}}><strong>{prod.name}</strong></h2>
                                </div>
                            </div>
                            ))}
                        </div>
                    </div>
                )
            }
        </div>
    );

}

export default Categories;