import { RouterProvider, createBrowserRouter } from 'react-router-dom';
import './App.css';
import Layout from './components/Layout/Layout';
import Contact from './components/Pages/Contact/Contact';
import Portfolio from './components/Pages/Portfolio/Portfolio';
import About from './components/Pages/About/About';
import NotFound from './components/Pages/NotFound/NotFound';
import Main from './components/Pages/Home/Main';
import Cart from './components/Pages/cart/cart';
import WishList from './components/Pages/wishList/wishList';
import Products from './components/Pages/Products/Products';
import Categories from './components/Pages/Categories/Categories';
import Brands from './components/Pages/Brands/Brands';
import Register from './components/Pages/Register/Register';
import Login from './components/Pages/Login/Login';
import ForgetPassword from './components/Pages/ForgetPassword/ForgetPassword';
import VerifyCode from './components/Pages/ForgetPassword/VerifyCode';
import ResetPassword from './components/Pages/ForgetPassword/ResetPassword';
import { useContext, useEffect } from 'react';
import { TokenContext } from './Context/TokenContext';
import ProtectedRoutes from './components/ProtectedRoutes/ProtectedRoutes';
import SingleProductDetails from './components/Pages/SingleProductDetails/SingleProductDetails';
import Payment from './components/Pages/Payment/Payment';

function App() {

  let {setToken} = useContext(TokenContext)

  useEffect(() => {
    if(localStorage.getItem("UserToken")){
      setToken(localStorage.getItem("UserToken"))
    }
  },[])

  let routes = createBrowserRouter([{
    path: "", element:<Layout/>, children:[
      {index: true, element: <ProtectedRoutes><Main/></ProtectedRoutes>},

      {path: "about", element: <ProtectedRoutes><About/></ProtectedRoutes>},

      {path: "portfolio", element: <ProtectedRoutes><Portfolio/></ProtectedRoutes>},

      {path: "contact", element: <ProtectedRoutes><Contact/></ProtectedRoutes>},

      {path: "cart", element: <ProtectedRoutes><Cart/></ProtectedRoutes>},

      {path: "wishList", element: <ProtectedRoutes><WishList/></ProtectedRoutes>},

      {path: "products", element: <ProtectedRoutes><Products/></ProtectedRoutes>},

      {path: "categories", element: <ProtectedRoutes><Categories/></ProtectedRoutes>},

      {path: "brands", element: <ProtectedRoutes><Brands/></ProtectedRoutes>},

      {path: "singleproductdetails/:id", element: <ProtectedRoutes><SingleProductDetails/></ProtectedRoutes>},
      {path: "Payment/:cartId", element: <ProtectedRoutes><Payment/></ProtectedRoutes>},


      {path: "register", element: <Register/>},
      {path: "login", element: <Login/>},
      {path: "forgetpassword", element: <ForgetPassword/>},
      {path: "verifycode", element: <VerifyCode/>},
      {path: "resetpassword", element: <ResetPassword/>},


      {path: "*", element:<NotFound/>},
    ] 
    }])

  return (

    <div>
      <RouterProvider router={routes}></RouterProvider>
    </div>
    
  );
}

export default App;
