function WishList(){

    const [wishList, setWishList] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [removedID, setRemovedID] = useState("");
    const [WishListIDs, setWishListIDs] = useState(JSON.parse(localStorage.getItem("WishListIDs")) || []);

    let {token} = useContext(TokenContext);

    async function getRemoveWishListItemId(id) {
        setRemovedID(id);
        setWishListIDs(prevIDs => prevIDs.filter(item => item !== id));
    }

    useEffect(() => {
        if (removedID !== "") {
            removeWishListItem(removedID, token)
                .then(() => getWishList(token))
                .catch(error => console.error('Error removing item and refreshing wishlist:', error));
        }
    }, [removedID, token]);

    async function removeWishListItem(removedID, token) {
        try {
            await axios.delete(`https://ecommerce.routemisr.com/api/v1/wishlist/${removedID}`, {
                headers: {
                    token: `${token}`
                }
            });
            toast.success(`Item removed successfully`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });
        } catch (error) {
            console.error('Error removing item from wishlist:', error);
            throw error;
        }
    }

    async function getWishList(token) {
        try {
            const response = await axios.get(`https://ecommerce.routemisr.com/api/v1/wishlist`, {
                headers: {
                    token: `${token}`
                }
            });
            if (response?.data?.status === 'success') {
                setWishList(response?.data?.data);
                setIsLoading(false);
            }
        } catch (error) {
            console.error('Error fetching wishlist:', error);
            throw error;
        }
    }

    useEffect(() => {
        localStorage.setItem("WishListIDs", JSON.stringify(WishListIDs)); // Save updated WishListIDs to localStorage
        getWishList(token);
    }, [WishListIDs, token]);

    function addToCart(productId){
        return axios.post('https://ecommerce.routemisr.com/api/v1/cart', {productId}, {
            headers:{
                token
            }
        })
    }

    let addToCartResult = useMutation(addToCart,{
        onSuccess: (addToCartResult)=>{
            toast.success(`${addToCartResult?.data?.message}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Zoom,
        });
        },
        onError: (addToCartResult)=>{
            toast.error(`${addToCartResult?.message}`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });  
        }
    });

    return(
        <div className="my-5 pt-5">
            <ToastContainer/>
            <div className="container pt-5 d-flex flex-column justify-content-center my-5 py-5 w-100" style={{backgroundColor: '#f0f3f2'}}>
                <h2 className="mx-3"><strong>My wish List</strong></h2>
                <div className="row">
                    {isLoading ? (<div className="d-flex align-items-center justify-content-center">
                        
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                    </div>): ""}
                   {wishList?.map((prod) => (
                        <div key={prod._id}>
                            <div className=" row py-2 px-3 d-flex align-items-center" style={{transition: 'all 0.5s', height: '250px', borderBottom: '1px solid #dee2e6'}}>
                                <div className="col-md-3 d-flex justify-content-center">
                                    <img src={prod.imageCover} className="w-50" height={150} alt=""/>
                                </div>
                                <div className="col-md-9 d-flex align-items-start justify-content-center flex-column">
                                    <h2 className="mb-3 "><strong>{prod.title}</strong></h2>
                                    <p className="mb-3 text-main">{prod.price} EGP</p>
                                    <button className="btn btn-danger" onClick={() => getRemoveWishListItemId(prod._id)}>Remove</button>
                                </div>
                                <button className="btn bg-main text-white w-100" onClick={(event) => {event.preventDefault(); addToCartResult.mutate(prod.id)}}>Add to cart</button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );

}

export default WishList;