import React, { useState } from "react";
import style from './Products.module.css';
import axios from "axios";
import { RotatingLines, Rings } from "react-loader-spinner";
import { useQuery } from "react-query";
import { Link } from "react-router-dom";

function Products(){

    const [input, setInput] = useState("");
    let searchArray = []

    function inputHandler(event) {
        setInput(event.target.value);
    }

    function getProducts(){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/products`)
    }

    let { data, isLoading} = useQuery("FeatureProducts", getProducts,)

    searchArray = data?.data?.data?.filter((prod) => prod.title.toLowerCase().includes(input.toLowerCase()))

    return(
        <div>
            <div className="container pt-5 d-flex align-items-center justify-content-center my-5 py-5">
                <div className="row">
                    {isLoading ? <div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                    </div>: ""}
                    <input type="text" name="searchinput" className="form-control mb-4" placeholder="Search" value={input} onChange={inputHandler}/>
                    {searchArray?.map((prod) => (
                        <div key={prod.id} className="col-md-3">
                            <div className="product py-2 px-3">
                                <Link to={`/singleproductdetails/${prod.id}`}>
                                    <img src={prod.imageCover} className="w-100" alt=""/>
                                    <p className="text-main">{prod.category.name}</p>
                                    <h2>{prod.title}</h2>
                                    <div className="d-flex justify-content-between">
                                        <p>{prod.price} EGP</p>
                                        <div>
                                            <i className="fa fa-star" style={{color: '#daa520'}}></i>
                                            {prod.ratingsAverage}
                                        </div>
                                    </div>
                                </Link>
                                <button className="btn bg-main text-white w-75" >Add to cart</button>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );

}

export default Products;