function FeaturedProducts(){

    const [input, setInput] = useState("");
    const [addedWID, setAddedWID] = useState("");
    const [addedCID, setAddedCID] = useState("");
    const [WishListIDs, setWishListIDs] = useState(JSON.parse(localStorage.getItem("WishListIDs")) || []);
    const [cartItems, setCartItems] = useState([]);

    let {token} = useContext(TokenContext);

    let searchArray = [];

    function inputHandler(event) {
        setInput(event.target.value);
    }

    function getProducts(){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/products`);
    }

    let { data, isLoading} = useQuery("FeatureProducts", getProducts,);

    searchArray = data?.data?.data?.filter((prod) => prod.title.toLowerCase().includes(input.toLowerCase()));

    async function addTOWishList(id){
        if (!WishListIDs.includes(id)) {
            setAddedWID(id);
            await addWishListItem(token, id);
            setWishListIDs(prevIDs => [...prevIDs, id]);
            localStorage.setItem("WishListIDs", JSON.stringify([...WishListIDs, id]));
            toast.success(`Item added to wishlist`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });
        } else {
            toast.warn(`Item already exists`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });
        }
    }

    async function addWishListItem(token, addedID) {
        try {
            let value = {
                productId: String(addedID)
            };
            const response = await axios.post(`https://ecommerce.routemisr.com/api/v1/wishlist`, value, {
                headers: {
                    token: `${token}`
                }
            });
            if (response.data.status === "success") {
                toast.success(`${response.data.message}`, {
                    position: "top-right",
                    autoClose: 5000,
                    hideProgressBar: true,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    transition: Zoom,
                });
            }
        } catch (error) {
            console.error('Error adding item to wishlist:', error);
        }
    }

    // async function addTOCart(id) {
    //     if (!cartItems.includes(id)) {
    //         setAddedCID(id);
    //         await addCartItem(id);
    //         setCartItems(prevItems => [...prevItems, id]);
    //     } else {
    //         toast.warn(`Item already exists in the cart`, {
    //             position: "top-right",
    //             autoClose: 5000,
    //             hideProgressBar: true,
    //             closeOnClick: true,
    //             pauseOnHover: true,
    //             draggable: true,
    //             progress: undefined,
    //             theme: "light",
    //             transition: Zoom,
    //         });
    //     }
    // }
    
    // async function addCartItem(addedID) {
    //     try {
    //         let value = {
    //             productId: String(addedID)
    //         };
    //         const response = await axios.post(`https://ecommerce.routemisr.com/api/v1/cart`, value, {
    //             headers: {
    //                 token: `${token}`
    //             }
    //         });
    //         if (response.data.status === "success") {
    //             toast.success(`${response.data.message}`, {
    //                 position: "top-right",
    //                 autoClose: 5000,
    //                 hideProgressBar: true,
    //                 closeOnClick: true,
    //                 pauseOnHover: true,
    //                 draggable: true,
    //                 progress: undefined,
    //                 theme: "light",
    //                 transition: Zoom,
    //             });
    //         }
    //     } catch (error) {
    //         console.error('Error adding item to cart:', error);
    //     }
    // }

    function addToCart(productId){
        return axios.post('https://ecommerce.routemisr.com/api/v1/cart', {productId}, {
            headers:{
                token
            }
        })
    }

    let addToCartResult = useMutation(addToCart,{
        onSuccess: (addToCartResult)=>{
            toast.success(`${addToCartResult?.data?.message}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Zoom,
        });
        },
        onError: (addToCartResult)=>{
            toast.error(`${addToCartResult?.message}`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });  
        }
    });


    useEffect(() => {
        if (token && addedWID) {
            addWishListItem(token, addedWID);
        }
    }, [token, addedWID]);

    // useEffect(() => {
    //     if (token && addedCID) {
    //         addCartItem(token, addedCID);
    //     }
    // }, [token, addedCID]);

    // 6428ebc6dc1175abc65ca0b9
    // 6428ebc6dc1175abc65ca0b9


    return(
        <div>
            <ToastContainer/>
            <div className="container pt-5 d-flex align-items-center justify-content-center my-5 py-5">
                <div className="row">
                    {isLoading ? <div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                    </div>: ""}
                    <input type="text" name="searchinput" className="form-control mb-4" placeholder="Search" value={input} onChange={inputHandler}/>
                    {searchArray?.map((prod) => (
                        <div key={prod._id} className="col-md-3">
                            <div className="product py-2 px-3">
                                <Link to={`/singleproductdetails/${prod._id}`}>
                                    <img src={prod.imageCover} className="w-100" alt=""/>
                                    <p className="text-main">{prod.category.name}</p>
                                    <h2>{prod.title}</h2>
                                    <div className="d-flex justify-content-between">
                                        <p>{prod.price} EGP</p>
                                        <div>
                                            <i className="fa fa-star" style={{color: '#daa520'}}></i>
                                            {prod.ratingsAverage}
                                        </div>
                                    </div>
                                </Link>
                                <div className="d-flex align-items-center justify-content-center">
                                    <button className="btn bg-main text-white w-75" onClick={(event) => {event.preventDefault(); addToCartResult.mutate(prod._id);}}>Add to cart</button>
                                    <i className="fa fa-solid fa-heart me-auto" style={{height: '28px', width: '28px', color: WishListIDs.includes(prod.id) ? 'red' : 'black'}} onClick={(event) => {event.preventDefault(); addTOWishList(prod.id)}}></i>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );

}

export default FeaturedProducts;