import React, { createContext, useState } from "react";

export const CartNumberContext = createContext();

const CartNumberContextProvider = (props) => {
    const [cartNumber, setCartNumber] = useState(null);

    return (
        <CartNumberContext.Provider value={{ cartNumber, setCartNumber }}>
            {props.children}
        </CartNumberContext.Provider>
    );
}

export default CartNumberContextProvider;