import React, { useContext, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import './NavBar.css'
import { library } from '@fortawesome/fontawesome-svg-core';
import { faCartShopping } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { TokenContext } from '../../Context/TokenContext';
import axios from 'axios';
import { useQuery } from 'react-query';

export default function NavBar() {

    let {token, setToken} = useContext(TokenContext)


    const [active, setActive] = useState('');
    let navigate = useNavigate();

    const isActive = (link) => {
        setActive(link);
    };

    function LogOut(){
        localStorage.removeItem("UserToken")
        setToken(null)
    }

    function getCart() {
        return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`, {
            headers:{
                token
            }
        });
    }

    let getCartResult = useQuery("getCart", getCart)

    return (
        <nav className="navbar navbar-expand-lg fixed-top">
            <div className="container">
                <Link className="navbar-brand" to="/">
                    <FontAwesomeIcon style={{color: '#4fa74f'}} icon={faCartShopping}/>
                    fresh cart
                </Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    {token ? <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className={`nav-link ${active === '' ? 'active' : ''}`} to="/" onClick={() => isActive('')}>Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'cart' ? 'active' : ''}`} to="/cart" onClick={() => isActive('cart')}>cart</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'wishList' ? 'active' : ''}`} to="/wishList" onClick={() => isActive('wishList')}>wish list</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'products' ? 'active' : ''}`} to="/products" onClick={() => isActive('products')}>products</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'categories' ? 'active' : ''}`} to="/categories" onClick={() => isActive('categories')}>categories</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'brands' ? 'active' : ''}`} to="/brands" onClick={() => isActive('brands')}>brands</Link>
                        </li>
                    </ul> : ""}
                    {token ? <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className={`nav-link position-relative ${active === 'cart' ? 'active' : ''}`} to="/cart" onClick={() => isActive('cart')}>
                                <i className='fa fa-solid fa-cart-shopping' >
                                    <div className='badge position-absolute top-0 end-0 text-white bg-main'>{getCartResult?.data?.data?.numOfCartItems}</div>
                                </i>
                            </Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'logout' ? 'active' : ''}`} to='/login' onClick={() => isActive('logout' , LogOut())}>Logout</Link>
                        </li>
                    </ul> 
                    :
                     <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'login' ? 'active' : ''}`} to="/login" onClick={() => isActive('login')}>Login</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${active === 'register' ? 'active' : ''}`} to="/register" onClick={() => isActive('register')}>Register</Link>
                        </li>
                    </ul>}
                </div>
            </div>
        </nav>
    );
}
