import React, { useContext } from 'react'

import { TokenContext } from '../../../Context/TokenContext';
import FeaturedProducts from '../../FeaturedProducts/FeaturedProducts';

import Slider from "react-slick";
import axios from 'axios';
import { useQuery } from 'react-query';

export default function Main() {
    
    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1, 
        arrows: false,
        autoplay: true
    }

    var CatSettings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 6,
        slidesToScroll: 1, 
        arrows: false,
        autoplay: true
    }

    let {token, setToken} = useContext(TokenContext)

    function getCategories(){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/categories`)
    }

    let { data } = useQuery("AllCategories", getCategories)
    return (

        <div>
            <>
                <div className='container'>
                    <div className='row mt-5 pt-5 justify-content-center'>
                        <div className='col-md-5 align-items-center justify-content-center'>
                            <Slider {...settings}>
                                <img src={`https://routeegy.github.io/Ecommerce/assets/images/41nN4nvKaAL._AC_SY200_.jpg`} height={300} alt=""/>
                                <img src={`https://routeegy.github.io/Ecommerce/assets/images/61cSNgtEISL._AC_SY200_.jpg`} height={300} alt=""/>
                            </Slider> 
                        </div>
                        <div className='col-md-3'>
                            <img src={`https://routeegy.github.io/Ecommerce/assets/images/XCM_Manual_1396328_4379575_Egypt_EG_BAU_GW_DC_SL_Bags_Wallets_379x304_1X._SY304_CB650636675_.jpg`} style={{height: '300px'}} className="w-100" alt=""/>
                            <img src={`https://routeegy.github.io/Ecommerce/assets/images/XCM_Manual_1533480_5305769_379x304_1X._SY304_CB616236518_.jpg`} style={{height: '300px'}} className="w-100" alt=""/>    
                        </div>
                    </div>
                </div>
            </>
            <>
               <Slider {...CatSettings}>
                    {data?.data?.data?.map((cat)=>
                        <div className='mt-5 pt-5' key={cat._id}>
                            <img src={cat.image} style={{height: '300px'}} className="w-100" alt=""/>
                            <p>{cat.name}</p>
                        </div>
                    )}
                </Slider> 
            </>
            

            <FeaturedProducts/>
        </div>

    )

}
