import React from "react";
import style from './Brands.module.css';
import { useQuery } from "react-query";
import axios from "axios";
import { Rings } from "react-loader-spinner";

function Brands(){

    function getBrands(){
        return axios.get(`https://ecommerce.routemisr.com/api/v1/brands`)
    }

    let { data, isLoading } = useQuery("AllCategories", getBrands)

    console.log(data?.data?.data);

    return(
        <div    >
            <h2 className="text-center text-main "><strong>All Brands</strong></h2>
            <div className="container pt-5 d-flex align-items-center justify-content-center mb-5 pb-5">
                <div className="row g-4">
                    {isLoading ? (<div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                    </div>): ""}
                   {data?.data?.data?.map((prod) => (
                        <div key={prod._id} className="col-md-3">
                            <div className="product py-2 px-3" style={{transition: 'all 0.5s'}}>
                                <div >
                                    <img src={prod.image}  className="w-100" alt=""/>
                                    <h2 className="text-center" style={{backgroundColor: 'white', padding: '16px'}}>{prod.name}</h2>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );

}

export default Brands;