import React, { useContext, useEffect, useState } from "react";
import style from './SingleProductDetails.module.css';
import { useParams } from "react-router-dom";
import { useMutation, useQuery } from "react-query";
import axios from "axios";
import { Rings } from "react-loader-spinner";
import Slider from "react-slick";
import { TokenContext } from "../../../Context/TokenContext";
import { ToastContainer, Zoom, toast } from "react-toastify";

function SingleProductDetails(){

    const [details, setDetails] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    let {token} = useContext(TokenContext);

    var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1, 
        arrows: false,
        autoplay: true
    }

    let {id} = useParams();
    

    async function getSingleProductData(id) {
        let { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`)
        setDetails(data?.data)
        setIsLoading(false)
    }

    useEffect(() => {
        getSingleProductData(id)
    },[])

    function addToCart(productId){
        return axios.post('https://ecommerce.routemisr.com/api/v1/cart', {productId}, {
            headers:{
                token
            }
        })
    }

    let addToCartResult = useMutation(addToCart,{
        onSuccess: (addToCartResult)=>{
            toast.success(`${addToCartResult?.data?.message}`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Zoom,
        });
        },
        onError: (addToCartResult)=>{
            toast.error(`${addToCartResult?.message}`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });  
        }
    });

    return(
        <div>
            <ToastContainer/>
            <div className="container my-5 py-5">
                <div>
                    {isLoading ? <div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                        </div> : 
                        
                        <div className="row align-items-center">
                        <div className="col-md-4 py-3">
                            <Slider {...settings}>
                                {details?.images?.map((img)=><img src={img} className="w-100" alt=""/>)}
                            </Slider>
                        </div>
                        <div className="col-md-8">
                            <h2><strong>{details?.title}</strong></h2>
                            <p>{details?.description}</p>
                            <div className="d-flex justify-content-between">
                                <p>{details?.price} EGP</p>
                                <div>
                                    <i className="fa fa-star" style={{color: '#daa520'}}></i>
                                    {details?.ratingsAverage}
                                </div>
                            </div>
                            <button className="btn bg-main text-white w-50" style={{marginLeft: '70px'}} onClick={(event) => {event.preventDefault(); addToCartResult.mutate(details.id)}}> + Add </button>
                        </div>
                    </div>}
                </div>
            </div>
        </div>
    );

}

export default SingleProductDetails;