import React, { useState } from "react";
import { useFormik } from "formik";
import * as Yup from 'yup';
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { Rings } from "react-loader-spinner";

function ResetPassword(){

    const [message, setMessage] = useState(null);
    const [errorMessage, setErrorMessage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    let navigate = useNavigate();

    let MyValSchema = Yup.object({
        email: Yup.string().email("Email is not Valid").required("Email is Required"),
        newPassword: Yup.string().required("Password is Required").matches(/^[A-z][a-z0-9]{8,}$/, "Password must be more than 8 Chars"),
    })

    const formik = useFormik({
        initialValues: {
            email: "",
            newPassword: "",
        }, validationSchema: MyValSchema,
        onSubmit: (values) => {
            formResetPassword(values)
        }
    })

    async function formResetPassword(values){
        setIsLoading(true)
        return await axios.put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`, values).then((data) => {
            console.log(data);
        if(data.data.token !== null){
            // setMessage(data.data.message)
            navigate("/")
            setIsLoading(false)
        }   
        }).catch((err) => {
            console.log(err);
            setErrorMessage(err.response.data.message)
            setIsLoading(false)
        })
        
    }

    return(
        <div>
            <div className="container  my-5 py-5">
                <div className="w-100 mx-auto">
                    {isLoading ? <div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                            </div>: ""}
                    <form onSubmit={formik.handleSubmit}>
                        <h2 className="mb-3"><strong>reset your account password</strong></h2>
                        {message ? <div className="alert alert-success">{message}</div> : ""}
                        {errorMessage ? <div className="alert alert-danger">{errorMessage}</div> : ""}

                        <label htmlFor="email">Email: </label>
                        {formik.touched.email && formik.errors.email ? <div className=" alert alert-danger">{formik.errors.email}</div> : ""}
                        <input type="email" name="email" id="email" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email}/>

                        <label htmlFor="newPassword">NewPassword: </label>
                        {formik.touched.newPassword && formik.errors.newPassword ? <div className=" alert alert-danger">{formik.errors.newPassword}</div> : ""}
                        <input type="password" name="newPassword" id="newPassword" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.newPassword}/>

                        {isLoading ? <button type="submit" className="btn bg-main text-white"><i className="fa fa-spin fa-spinner"></i></button> : <button type="submit" disabled={!(formik.isValid && formik.dirty)} className="btn btn-outline-success">reset password</button>}                        
                        
                    </form>
                </div>
            </div>
        </div>
    );

}

export default ResetPassword;