import React, { useContext, useEffect, useState } from "react";
import style from './cart.module.css';
import axios from 'axios';
import { useMutation, useQuery, useQueryClient } from 'react-query';
import { Rings } from "react-loader-spinner";
import { TokenContext } from "../../../Context/TokenContext";
import { ToastContainer, Zoom, toast } from "react-toastify";
import { Link, useNavigate } from "react-router-dom";

function Cart(){

    const [cart, setCart] = useState([])
    const [isLoading, setIsLoading] = useState(true);
    const [removedID, setRemovedID] = useState("")

    let {token} = useContext(TokenContext);

    let navigate = useNavigate();

    // async function getRemoveCartItemId(id) {
    // setRemovedID(id);
    // }

    // useEffect(() => {
    //     if (removedID !== "") {
    //         removeCartItem(removedID, token)
    //             .then(() => getCart(token))
    //             .catch(error => console.error('Error removing item and refreshing cart:', error));
    //     }
    // }, [removedID, token]);

    // async function removeCartItem(removedID, token) {
    //     try {
    //         await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${removedID}`, {
    //             headers: {
    //                 token: `${token}`
    //             }
    //         });
    //     } catch (error) {
    //         console.error('Error removing item from cart:', error);
    //         throw error;
    //     }
    // }

    // async function getCart(token) {
    //     try {
    //         const response = await axios.get(`https://ecommerce.routemisr.com/api/v1/cart`, {
    //             headers: {
    //                 token: `${token}`
    //             }
    //         });
    //         if (response?.data?.status === 'success') {
    //             setCart(response?.data?.data);
    //             setIsLoading(false);
    //         }
    //     } catch (error) {
    //         console.error('Error fetching cart:', error);
    //         throw error;
    //     }
    // }

    // useEffect(() => {
    //     getCart(token);
    // }, [token]);

    //get Cart
    function getCart() {
        return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`, {
            headers:{
                token
            }
        });
    }

    let getCartResult = useQuery("getCart", getCart)

    //remove from Cart
    function removeCartItem(id) {
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`, {
            headers:{
                token
            }
        });
    }
    const queryClient = useQueryClient()
    let removeCartItemResult = useMutation(removeCartItem,{
        onSuccess: ()=>{
            toast.success(`item removed successfully`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Zoom,
            });
            queryClient.invalidateQueries("getCart")
        },        
    })

    //update Cart Item's count
    function updateCartItemCount({id, count}) {
        return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id}`, {count}, {
            headers:{
                token
            }
        });
    }

    let updateCartItemCountResult = useMutation(updateCartItemCount,{
        onSuccess: ()=>{
            toast.success(`item count updated successfully`, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: true,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
            theme: "light",
            transition: Zoom,
            });
            queryClient.invalidateQueries("getCart")
        },        
    })

    // async function clearCart(token) {
    //     try {
    //         const response = await axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`, {
    //             headers: {
    //                 token: `${token}`
    //             }
    //         });
    //         console.log(response);
    //     } catch (error) {
    //         console.error('Error fetching cart:', error);
    //         throw error;
    //     }
    // }

    //clear Cart
    function clearCart() {
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`, {
            headers:{
                token
            }
        });
    }

    let clearCartResult = useMutation(clearCart,{
        onSuccess: ()=>{
            toast.success(`Cart is now empty`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: true,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                transition: Zoom,
            });
            queryClient.invalidateQueries("getCart");
            navigate("/");
            window.location.reload();
        },        
    })

    return(
        <div>
            <ToastContainer/>
            <div className="my-5 pt-5">
            <div className="container pt-5 d-flex flex-column justify-content-center my-5 py-5 w-100" style={{backgroundColor: '#f0f3f2'}}>
                { getCartResult?.data?.data?.data?.totalCartPrice ? <>
                    <h2 ><strong>Cart Shop</strong></h2>
                    <div className="row d-flex flex-row align-items-center justify-content-between px-2">
                        <div className="col-md-3">
                        <p><strong>total price: <span className="text-main"> { getCartResult?.data?.data?.data?.totalCartPrice }</span></strong></p>
                        </div>
                        <div className="col-md-2">
                            <button className="col-md-7 btn btn-primary"><Link to={`/Payment/${getCartResult?.data?.data?.data?._id}`}>CheckOut</Link></button>
                            <p><strong>total number of items: <span className="text-main">{ getCartResult?.data?.data?.numOfCartItems }</span></strong></p>
                        </div>
                        </div>
                <div className="row">
                    {getCartResult?.isLoading ? (<div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                    </div>) : ""}
                   {getCartResult?.data?.data?.data?.products?.map((prod) => (
                        <div key={prod?.product?.id}>
                            <div className=" row d-flex align-items-center" style={{transition: 'all 0.5s', height: '250px', borderBottom: '1px solid #dee2e6'}}>
                                <div className="col-md-2 d-flex justify-content-center">
                                    <img src={prod.product.imageCover} className="w-100" height={150} alt=""/>
                                </div>
                                <div className="col-md-8 d-flex align-items-start justify-content-center flex-column">
                                    <h2 className="mb-3 "><strong>{prod.product.title}</strong></h2>
                                    <p className="mb-3 text-main">{prod.price} EGP</p>
                                    <button className="btn btn-danger fa fa-trash" onClick={(e) => {removeCartItemResult?.mutate(prod?.product.id);}}> Remove</button>
                                </div>
                                <div className="col-md-2 d-flex align-items-start justify-content-center">
                                    <button className="btn btn-outline-success" onClick={()=>{updateCartItemCountResult?.mutate({id: prod?.product._id, count: prod?.count+1});}}><span className="text-dark">+</span></button>
                                    <p className="text-main mt-2 mx-3">{prod.count}</p>
                                    <button className="btn btn-outline-success" onClick={() => {
                                        const newCount = prod.count - 1;
                                        if (newCount <= 0) {
                                            removeCartItemResult.mutate(prod.product.id);
                                        } else {
                                            updateCartItemCountResult.mutate({ id: prod.product._id, count: newCount });
                                        }
                                    }}>
                                        <span className="text-dark">-</span>
                                    </button>
                                </div>
                            </div>
                        </div>
                        // console.log(prod?.count)
                    ))}
                </div>
                <button className="btn btn-outline-danger w-50 align-self-center mt-3" onClick={()=>{clearCartResult?.mutate()}}>Clear Cart</button>
                </> : <h2>your cart is empty</h2>}
            </div>
        </div>
        </div>
    );

}

export default Cart;