import React, { useState } from "react";
import style from './ForgetPassword.module.css';
import { useFormik } from "formik";
import * as Yup from 'yup';
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { Rings } from "react-loader-spinner";

const Loader = () => {
    return (
      <div className={style['loader-overlay']}>
        <div className={style['loader-dot']}></div>
        <div className={style['loader-dot']}></div>
        <div className={style['loader-dot']}></div>
      </div>
    );
};

function ForgetPassword(){

    const [message, setMessage] = useState(null);
    const [errorMessage, setErrorMessage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    let navigate = useNavigate();

    let MyValSchema = Yup.object({
        email: Yup.string().email("Email is not Valid").required("Email is Required"),
    })

    const formik = useFormik({
        initialValues:{
            email: ""
        }, validationSchema: MyValSchema,
        onSubmit:(values) => {
            formForgetPassword(values)
        }
    })

    async function formForgetPassword(values){
        setIsLoading(true)
        return await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords`, values).then((data) => {
        if(data.data.message === "Reset code sent to your email"){
            setMessage(data.data.message)
            navigate("/verifycode")
            setIsLoading(false)
        }   
        }).catch((err) => {
            setErrorMessage(err.response.data.message)
            setIsLoading(false)
        })
        
    }

    return(
        <div style={{height: '40vh'}}>
            <div className="container  my-5 py-5">
                <div className="w-100 mx-auto">
                    {isLoading ? <div className="d-flex align-items-center justify-content-center">
                        <Rings
                            visible={true}
                            height="480"
                            width="480"
                            color="#4fa94d"
                            ariaLabel="rings-loading"
                            wrapperStyle={{}}
                            wrapperClass=""
                        />
                            </div>: ""}
                    <form onSubmit={formik.handleSubmit}>
                        <h2 className="mb-3"><strong>please enter your Email</strong></h2>
                        {message ? <div className="alert alert-success">{message}</div> : ""}
                        {errorMessage ? <div className="alert alert-danger">{errorMessage}</div> : ""}

                        {formik.touched.email && formik.errors.email ? <div className=" alert alert-danger">{formik.errors.email}</div> : ""}
                        <input type="email" name="email" id="email" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email} placeholder="Email"/>

                        {/* {isLoading ? <button type="submit" className="btn bg-main text-white"><i className="fa fa-spin fa-spinner"></i></button> : <button type="submit" disabled={!(formik.isValid && formik.dirty)} className="btn btn-outline-success">Submit</button>}                         */}
                        <button type="submit" disabled={!(formik.isValid && formik.dirty)} className="btn btn-outline-success">Submit</button>                        

                    </form>
                </div>
            </div>
        </div>
    );

}

export default ForgetPassword;