import React, { useState } from "react";
import style from './Register.module.css';
import { useFormik } from "formik";
import * as Yup from 'yup';
import axios from "axios";
import { useNavigate } from "react-router-dom";

function Register(){

    const [message, setMessage] = useState(null);
    const [errorMessage, setErrorMessage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    let navigate = useNavigate();

    let MyValSchema = Yup.object({
        name: Yup.string().required("Name is Required").min(5, "Min is 5 Chars").max(20, "Max is 20 Chars"),
        email: Yup.string().email("Email is not Valid").required("Email is Required"),
        password: Yup.string().required("Password is Required").matches(/^[A-z][a-z0-9]{8,}$/, "Password must be more than 8 Chars"),
        rePassword: Yup.string().required("Repassword is Required").oneOf([Yup.ref("password")], "Password not match"),
        phone: Yup.string().required("Phone is Required").matches(/^01[0125][0-9]{8}$/, "Phone must be 11 Numbers")
    })

    const formik = useFormik({
        initialValues: {
            name: "",
            email: "",
            password: "",
            rePassword: "",
            phone: ""
        }, validationSchema: MyValSchema,
        onSubmit: (values) => {
            formRegister(values)
        }
    })

    async function formRegister(values){
        setIsLoading(true)
        return await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup`, values).then((data) => {
        if(data.data.message === "success"){
            setMessage(data.data.message)
            navigate("/login")
            setIsLoading(false)
        }   
        }).catch((err) => {
            setErrorMessage(err.response.data.message)
            setIsLoading(false)
        })
        
    }

    return(
        <div>
            <h2>Register</h2>
            <div className="container  my-5 py-5">
                <div className="w-75 mx-auto">
                    <form onSubmit={formik.handleSubmit}>
                        <h2>Register Now</h2>
                        {message ? <div className="alert alert-success">{message}</div> : ""}
                        {errorMessage ? <div className="alert alert-danger">{errorMessage}</div> : ""}
                        <label htmlFor="name">Name: </label>
                        {formik.touched.name && formik.errors.name ? <div className=" alert alert-danger">{formik.errors.name}</div> : ""}
                        <input type="text" name="name" id="name" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.name}/>

                        <label htmlFor="email">Email: </label>
                        {formik.touched.email && formik.errors.email ? <div className=" alert alert-danger">{formik.errors.email}</div> : ""}
                        <input type="email" name="email" id="email" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email}/>

                        <label htmlFor="password">Password: </label>
                        {formik.touched.password && formik.errors.password ? <div className=" alert alert-danger">{formik.errors.password}</div> : ""}
                        <input type="password" name="password" id="password" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.password}/>

                        <label htmlFor="rePassword">Repassword: </label>
                        {formik.touched.rePassword && formik.errors.rePassword ? <div className=" alert alert-danger">{formik.errors.rePassword}</div> : ""}
                        <input type="password" name="rePassword" id="rePassword" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.repassword}/>

                        <label htmlFor="phone">Phone: </label>
                        {formik.touched.phone && formik.errors.phone ? <div className=" alert alert-danger">{formik.errors.phone}</div> : ""}
                        <input type="text" name="phone" id="phone" className="form-control mb-4" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.phone}/>

                        {isLoading ? <button type="submit" className="btn bg-main text-white"><i className="fa fa-spin fa-spinner"></i></button> : <button disabled={!(formik.isValid && formik.dirty)} type="submit" className="btn bg-main text-white">Register</button>}                        
                    </form>
                </div>
            </div>
        </div>
    );
}

export default Register;