import React, { useContext, useState } from "react";
import style from './Login.module.css';
import { useFormik } from "formik";
import * as Yup from 'yup';
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";
import { TokenContext } from "../../../Context/TokenContext";

function Login(){

    let {token, setToken} = useContext(TokenContext);

    const [message, setMessage] = useState(null);
    const [errorMessage, setErrorMessage] = useState(null);
    const [isLoading, setIsLoading] = useState(false);
    let navigate = useNavigate();

    let MyValSchema = Yup.object({
        email: Yup.string().email("Email is not Valid").required("Email is Required"),
        password: Yup.string().required("Password is Required").matches(/^[A-z][a-z0-9]{8,}$/, "Password must be more than 8 Chars"),
    })

    const formik = useFormik({
        initialValues: {
            email: "",
            password: "",
        }, validationSchema: MyValSchema,
        onSubmit: (values) => {
            formLogin(values)
        }
    })

    async function formLogin(values){
        setIsLoading(true)
        return await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signin`, values).then((data) => {
        if(data.data.message === "success"){
            setMessage(data.data.message)
            setToken(data.data.token)
            localStorage.setItem("UserToken", data.data.token)
            navigate("/")
            setIsLoading(false)
        }   
        }).catch((err) => {
            setErrorMessage(err.response.data.message)
            setIsLoading(false)
        })
        
    }

    return(
        <div>
            <div className="container  my-5 py-5">
                <div className="w-75 mx-auto">
                    <form onSubmit={formik.handleSubmit}>
                        <h2>Login now</h2>
                        {message ? <div className="alert alert-success">{message}</div> : ""}
                        {errorMessage ? <div className="alert alert-danger">{errorMessage}</div> : ""}

                        <label htmlFor="email">Email: </label>
                        {formik.touched.email && formik.errors.email ? <div className=" alert alert-danger">{formik.errors.email}</div> : ""}
                        <input type="email" name="email" id="email" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.email}/>

                        <label htmlFor="password">Password: </label>
                        {formik.touched.password && formik.errors.password ? <div className=" alert alert-danger">{formik.errors.password}</div> : ""}
                        <input type="password" name="password" id="password" className="form-control mb-3" onChange={formik.handleChange} onBlur={formik.handleBlur} value={formik.values.password}/>

                        <div className="d-flex justify-content-between align-items-center">
                            <Link to='/forgetpassword'><strong>Forget Your Password?</strong></Link>
                            {isLoading ? <button type="submit" className="btn bg-main text-white"><i className="fa fa-spin fa-spinner"></i></button> : <button type="submit" disabled={!(formik.isValid && formik.dirty)} className="btn bg-main text-white">Login</button>}                        
                        </div>
                        
                        
                    </form>
                </div>
            </div>
        </div>
    );

}

export default Login;